@import url(https://fonts.googleapis.com/css2?family=Mulish:ital@1&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif, 'Muli';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.navbar{
  background-color: white;
}

.navbar-brand{
  font-size: 1.9rem !important;
  color: blueviolet !important ;
}
.navbar a{
  font-size: 1.3rem;
  text-transform: capitalize;
}
.navbar a:hover{
color: blue !important;
}

.menu_active{
  font-weight: bold;
  border-bottom: 1px solid #565387;
}
code {
  font-family: Muli, source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.out{
  font-weight: bold;
  color: #f58a10;
}
.work{
  font-weight: bold;
  color: #0e31f5;
}
.harder{
  font-weight: bold;
  color: #f00f0f;
}
#header{
  width: 100%;
  height: 80vh;
  margin-top: 70px;
}
#header h1{
  margin: 0 0 10 px 0;
  font-size: 48px;
  font-weight: 700;
  color: black;
}
#header .brand-name{
  color: blue;
}

#header h2{
  margin: 0 0 10 px 0;
  font-size: 48px;
  font-weight: 700;
  color: black;
}
#header .btn-get-started{
  font-weight: bold;
  font-weight: 500;
  font-size: 16px;
  letter-spacing: 1px;
  display: inline-block;
  padding: 10px 30px;
  border-radius: 50px;
  transition: 0.5s;
  color: #3498db;
  border: 2px solid #3498db;
  text-decoration: none;
}

#header .btn-get-started:hover {
  background: #3498db;
  color: #ffffff;
}
.header-img{
  text-align: right;
}
#header .animated{
  animation: up-down 2s ease-in-out infinite alternate-reverse both;
}
@-webkit-keyframes up-down{
  0%{
    transform: translateY(10px);
  }
  100%{
    transform: translateY(-10px);
  }
}
@keyframes up-down{
  0%{
    transform: translateY(10px);
  }
  100%{
    transform: translateY(-10px);
  }
}

/* Responsive Code */

@media(max-width: 991px)
{
  #header {
    height: 80vh;
    text-align: center;
  }
}

#header .header-img{
  text-align: center;
}
#header .header-img img{
  width: 60%;
}
@media(max-width: 768px)
{
  #header {
    margin-top: 20px;
  }
#header h1{
  font-size: 28px;
}
#header h2{
  font-size: 18px;
  margin-bottom: 30px;
}
#header .header-img img{
  width: 70%;
}
}
.card-img-top{
  width: 100%;
  height: 15vw;
  object-fit: cover;

}
.carousel-control-prev-icon,
.carousel-control-next-icon {
  height: 100px;
  width: 100px;
  outline: black;
  background-size: 100%, 100%;
  border-radius: 50%;
  border: 1px solid black;
  background-image: none;
}

.carousel-control-next-icon:after
{
  content: '>';
  font-size: 55px;
  font-weight: bold;
  color: rgb(30, 255, 0);
}

.carousel-control-prev-icon:after {
  content: '<';
  font-size: 55px;
  font-weight: bold;
  color: red;
}
.stylemail{
font-size: 20px;
color: red;
}
.bg-new{
  background: #000;
}
.tempstyle{
color: blue;
}
#progress{
  width: 100%;
}
.paraclass{
  font-weight: bold;
}

body {
  background-color: #2b2b2b;
  color: #FFF;
  font-family: 'Roboto', sans-serif;
}

.container {
  max-width: 500px;
  margin: 50px auto;
}
  
  .progressbar-container {
    position: relative;
    width: 100%;
    height: 50px;
    border: 5px solid rgb(4, 21, 116);
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;}
    .progressbar-complete 
    {
      position: absolute;
      left: 0;
      top: 0px;
      height: 100%;
      background-color: #5225bd;
      border-radius: 10px;
      -webkit-animation: g 2500ms infinite ease-in-out;
              animation: g 2500ms infinite ease-in-out;
      z-index: 2;}
      .progressbar-liquid {
        z-index: 1;
        width: 70px;
        height: 70px;
        -webkit-animation: g 2500ms infinite ease-in-out, r 3000ms infinite cubic-bezier(0.5, 0.5, 0.5, 0.5);
                animation: g 2500ms infinite ease-in-out, r 3000ms infinite cubic-bezier(0.5, 0.5, 0.5, 0.5);
        position: absolute;
        right: -5px;
        top: -10px;
        background-color: #5225bd;
        border-radius: 40%;
      }
    
    .progress {
      z-index: 2;
    }
  


@-webkit-keyframes g {
  0% { background-color: #5225bd; }
  50% { background-color: #607e9e; }
  100% { background-color: #5225bd; }
}
  


@keyframes g {
  0% { background-color: #5225bd; }
  50% { background-color: #607e9e; }
  100% { background-color: #5225bd; }
}

@-webkit-keyframes r {
  from { transform: rotate(0deg); }
  from { transform: rotate(360deg); }
}

@keyframes r {
  from { transform: rotate(0deg); }
  from { transform: rotate(360deg); }
}
.bgcolor{
  color: rgb(235, 235, 235);
}
.bgcoloradv{
  background-color: rgb(7, 6, 8);
}

.center {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 30%;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

